<template>
  <div class="dashboard">
    <section class="goCard">
      <a-card style="min-width: 100%" title="快捷方式">
        <a-card-grid
          class="card-item"
          v-for="short in shortcut"
          :key="short.path"
          @click="onCardClick(short.name)"
        >
          <svg-icon
            style="font-size: 48px; margin-right: 5px"
            :icon-class="short.icon"
          />{{ short.name }}
        </a-card-grid>
        <a-card-grid class="card-item" @click="addGoCard()">
          <svg-icon
            style="font-size: 48px; margin-right: 5px"
            icon-class="add"
          />新增快捷方式
        </a-card-grid>
      </a-card>
      <a-modal
        title="新增快捷方式"
        :visible="visible"
        :confirm-loading="confirmLoading"
        @ok="handleOk"
        @cancel="handleCancel"
      >
        <template>
          <a-input v-model="myGoCard.name" placeholder="快捷方式名称" />
          <a-cascader
            style="width:100%;margin-top:1rem"
            :options="options"
            placeholder="选择快捷对象"
            @change="onChange"
          />
        </template>
      </a-modal>
    </section>
    <div>{{ timeState }}</div>
  </div>
</template>

<script>
import { getTimeState } from '@/utils/time.js'
import { message } from 'ant-design-vue'

export default {
  name: 'Dashboard',
  data () {
    return {
      visible: false,
      confirmLoading: false,
      myGoCard: {
        name: '',
        path: ''
      },
      options: [
        {
          value: '/dashboard',
          label: '仪表盘'
        },
        {
          value: 'jiangsu',
          label: '示例',
          children: [
            {
              value: 'nanjing',
              label: '表格',
              children: [
                {
                  value: 'zhonghuamen',
                  label: 'Zhong Hua Men'
                }
              ]
            }
          ]
        }
      ]
    }
  },
  created () {
    this.$store.dispatch('user/getShortcut').then(() => {})
    this.handleOptions(this.$router.options.routes)
  },
  computed: {
    shortcut () {
      return this.$store.getters.shortcut
    },
    timeState () {
      return getTimeState()
    }
  },
  methods: {
    onCardClick (data) {
      message.info(`跳转${data}路由`)
    },
    addGoCard () {
      this.visible = true
    },
    handleOk (e) {
      this.ModalText = 'The modal will be closed after two seconds'
      this.confirmLoading = true
      setTimeout(() => {
        this.visible = false
        this.confirmLoading = false
      }, 2000)
    },
    handleCancel (e) {
      this.visible = false
    },
    onChange (value) {
      console.log(value)
    },
    handleOptions (routes) {
      // console.log(routes)
      routes.forEach((item) => {
        // console.log(item)
        if (!item.hidden) {
          if (item.children && item.meta) {
            this.options.push()
          }
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.dashboard {
  .goCard {
    display: flex;
    justify-content: space-between;
    .card-item {
      width: 20%;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }
}
</style>
